import React from "react";
import GoogleMapReact from "google-map-react";

const AnyReactComponent = ({ text }) => <div className="map-style">{text}</div>;

function Map() {
  const defaultProps = {
    center: {
      lat: 59.43598,
      lng: 24.84722,
    },
    zoom: 15,
  };

  return (
    <div style={{ height: "50vh", width: "100%" }}>
      <GoogleMapReact
        // bootstrapURLKeys={{ key: apiKey }}
        defaultCenter={defaultProps.center}
        defaultZoom={defaultProps.zoom}
      >
        <AnyReactComponent
          lat={59.43598}
          lng={24.84722}
          text={<i class="bi bi-geo-alt-fill">SALMON LOVER</i>}
        />
      </GoogleMapReact>
    </div>
  );
}
export default Map;
